:global(.mobile) {
  .root {
    margin: 28px 16px 0;
    .balanceVal {
      font-size: 48px;
    }
  }
}

.root {
  background: #ffffff;
  border-radius: 8px;
  padding: 30px 20px 20px;
  line-height: 1;
  height: 100%;

  .orderInput {
    input:focus {
      box-shadow: none;
    }
  }

  :global {
    .ant-input-affix-wrapper .ant-input-suffix {
      right: 0;
    }
  }
  & > h2 {
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    color: #333333;
    span {
      font-weight: normal;
    }
  }

  input {
    height: 43px;
    border: 0;
    border-bottom: 1px solid #e5e5e5;
    border-radius: 0;
    padding: 0;
    font-size: 24px;
    box-shadow: none;
  }

  .balanceVal {
    text-align: center;
    font-size: 42px;
    font-weight: 500;
    color: #1346ff;
    margin: 20px 0 10px;
  }

  .dayChange {
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    color: #333333;
    span {
      color: #1346ff;
    }
  }

  .actionLink {
    font-size: 14px;
    font-weight: 500;
    color: #1346ff;
    margin: 20px 0;
    button {
      padding: 0;
      height: 1em;
    }
  }

  .radioBtn {
    :global {
      .ant-radio-group {
        width: 100%;
        text-align: center;
      }
      .ant-radio-button-wrapper:hover {
        color: #15b384;
      }
      .ant-radio-button-wrapper-checked {
        border-color: #15b384;
        color: #15b384;
        box-shadow: -1px 0 0 0 #15b384;
      }
      .ant-radio-button-wrapper:nth-child(2):hover {
        color: #f55858;
      }
      .ant-radio-button-wrapper-checked:nth-child(2) {
        border-color: #f55858;
        color: #f55858;
        box-shadow: -1px 0 0 0 #f55858;
      }

      .ant-radio-button-wrapper {
        width: 50%;
        border-radius: 0;
        height: 36px;
        line-height: 36px;
      }
    }
  }
  .price {
    font-size: 14px;
    font-weight: 500;
    color: #333333;
    margin: 20px 0 15px;
  }

  .utilMax {
    font-size: 14px;
    font-weight: 500;
    color: #333333;
    margin: 15px 0;
  }
  .amountTip {
    font-size: 14px;
    font-weight: 500;
    color: #333;
    margin-bottom: 15px;
  }

  .settlement {
    font-size: 14px;
    font-weight: 500;
    color: #333333;
    // margin: 15px 0;
  }
  .red {
    color: #f55858;
  }
  .green {
    color: #15b384;
  }
  button {
    width: 100%;
  }
}

