@import '../sections/variables.less';

:global(.mobile) {
  .root {
    padding: 0 16px;
    margin: 0 auto;
    & > h2 {
      font-size: 24px;
      margin: 56px 0 15px;
    }
  }
}
.root {
  max-width: @maxWidth;
  margin: 0 auto 80px;
  position: relative;

  & > h2 {
    font-size: 32px;
    color: #383838;
    line-height: 40px;
    margin-bottom: 0;
    position: absolute;
    top: 15px;
    line-height: 64px;
    z-index: 2;
  }

  .orderTab{
    padding-top: 10px;
    background-color: #f9fafc;
    & > div {
      border-bottom: 0;
      text-align: right;
    }
    
    & > div:first-child{
      & > div {
        padding: 10px 20px;
      }
    }
  }
  .tableWpr {
    background: #ffffff;
    border-radius: 8px;
  }

  button {
    height: 28px;
    font-size: 14px;
    background-color: rgba(19, 70, 255, 0.07);
    &:hover {
      background-color: rgba(19, 70, 255, 0.07);
    }
    border-radius: 2px;
  }

  :global {
    .ant-table-thead th {
      background-color: #fff;
      font-weight: normal;
      font-size: 14px;
      color: #808080;
      font-weight: 500;
      line-height: 20px;
    }
    .ant-table-tbody td {
      padding: 23px 20px;
      font-size: 14px;
      font-weight: 500;
      color: #383838;
      line-height: 22px;
    }
    .ant-table-tbody td,
    .ant-table-thead th,
    .ant-table-thead td {
      border-color: #f9f9f9;
    }
    .ant-table-body {
      border-radius: 8px 8px 0 0;
    }
    .ant-table-placeholder {
      border-bottom: none;
      border-radius: 0 0 8px 8px;
    }
    .ant-table-content {
      box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.05);
      padding: 20px;
      background-color: #FFF;
    }
  }
}

